.coming-bg {
  background-image: url("../images/comingsoon-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.timeline {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  padding-left: 3rem;
}
.timeline:before {
  content: "";
  position: absolute;
  display: block;
  height: 100%;
  width: 2px;
  background: var(--bs-secondary-bg);
  top: 0;
  left: 1.5rem;
  transform: translateX(-50%);
  border-radius: 0.375rem;
}

.timeline-item {
  position: relative;
  width: 100%;
}

.timeline-pin {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  left: -3rem;
  top: 50%;
  transform: translateY(-50%);
}
.timeline-pin .marker-circle {
  width: 0.8rem;
  height: 0.75rem;
}

.timeline-content {
  width: 100%;
  color: var(--bs-timeline-content-color);
  padding: 0.5rem;
}
.timeline-content .rich-list-item {
  margin-bottom: 0;
}

.timeline-timed {
  padding-left: 7rem;
}
.timeline-timed:before {
  left: 5.5rem;
}
.timeline-timed .timeline-time {
  position: absolute;
  width: 4rem;
  left: -7rem;
  font-size: 1rem;
  font-weight: 700;
  padding: 0.25rem;
  top: 50%;
  transform: translateY(-50%);
}

.timeline-zigzag {
  padding-left: 0;
}
.timeline-zigzag:before {
  left: 50%;
  transform: translateX(-50%);
}
.timeline-zigzag .timeline-item {
  background-color: var(--bs-secondary-bg);
  border-radius: 0.3rem;
  width: calc(50% - 1.5rem);
}
.timeline-zigzag .timeline-item:nth-child(odd) {
  align-self: flex-start;
}
.timeline-zigzag .timeline-item:nth-child(odd) .timeline-pin {
  left: auto;
  right: -3rem;
}
.timeline-zigzag .timeline-item:nth-child(even) {
  align-self: flex-end;
}

@media (max-width: 520px) {
  .timeline-zigzag {
    margin-left: 1rem;
  }
  .timeline-zigzag .timeline-item {
    margin-bottom: 0.5rem;
    width: 100%;
  }
  .timeline-zigzag .timeline-item:nth-child(even) {
    align-self: flex-start;
  }
  .timeline-zigzag .timeline-item:nth-child(even) .timeline-pin {
    left: -33px;
  }
  .timeline-zigzag .timeline-item:nth-child(odd) .timeline-pin {
    right: 100%;
    left: -33px;
  }
  .timeline-zigzag::before {
    left: -9px;
  }
}
.horizontal-timeline {
  position: relative;
  width: 100%;
  margin: 0 auto;
}
.horizontal-timeline::before {
  content: "";
  position: absolute;
  width: 100%;
  top: 210px;
  left: 0;
  height: 2px;
  background-color: var(--bs-secondary-bg);
}
.horizontal-timeline .item-box {
  max-width: 350px;
  margin: 250px 0 0;
  background-color: transparent;
  box-shadow: none;
  border-bottom: 2px solid var(--bs-border-color);
}
.horizontal-timeline .item-box:nth-child(even) {
  margin-top: 5px;
}
.horizontal-timeline .item-box:nth-child(even) .timeline-content {
  transform: rotate(-180deg);
}
.horizontal-timeline .item-box:nth-child(even) .timeline-content .timeline-text {
  transform: rotate(180deg);
}
.horizontal-timeline .item-box:nth-child(even) .time {
  top: 196px;
}
@media (max-width: 1800px) {
  .horizontal-timeline .item-box:last-child {
    display: none;
  }
}
@media (max-width: 1568px) {
  .horizontal-timeline .item-box:nth-child(5n) {
    display: none;
  }
}
@media (max-width: 1407px) {
  .horizontal-timeline .item-box:nth-child(5n-1) {
    display: none;
  }
}
.horizontal-timeline .timeline-content {
  border-bottom: 2px solid;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background-color: var(--bs-secondary-bg);
}
.horizontal-timeline .timeline-content::before {
  content: "";
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  left: 0;
  top: -23px;
  border-color: transparent transparent var(--bs-secondary-bg) transparent;
  border-width: 12px;
  right: 0;
  margin: 0 auto;
}
.horizontal-timeline .time {
  position: absolute;
  top: -48px;
  right: 0;
  left: 0;
  margin: 0 auto;
}

@media (max-width: 826px) {
  .hori-timeline {
    display: none;
  }
}
